div.testimonial {
  position: relative;
  background-color: #fff;
  color: #222629;
  font-size: 23px;
  padding: 33px;
  text-align: left;
  border-radius: 10px;
  box-shadow: 0px 3px 30px #57677217;
}

div.dave {
  border: 1px solid #A7B6C1;
  position: relative;
	background: #FFF;
  padding:50px 20px;
  margin-left:20px;
  margin-top:20px;
  width:auto;
  display:inline-block;
}

div.dave-arrow {
  color: red;
}

div.dave-arrow:after, div.dave-arrow:before {
	right: 100%;
	top: 50%;
	border: solid transparent;
	content: " ";
	height: 0;
	width: 0;
	position: absolute;
	pointer-events: none;
}

div.dave-arrow::before {
	border-color: rgba(12, 245, 167, 0);
	border-top-color: #A7B6C1;
	border-width: 51px;
  margin-top: 62px;
  left: 54px;
}

div.dave-arrow::after {
	border-color: rgba(40, 213, 127, 0);
	border-top-color: #FFF;
	border-width: 50px;
  margin-top: 61px;
  left: 55px;
}

div.brooke {
  border: 1px solid #50B9D3;
}

div.stone {
  border: 1px solid #42575F;
}

